

























































button:focus, button:active {
  outline: 0; }

button {
  text-align: center; }

.circle {
  border-radius: 100%; }

.card--flex-toolbar {
  margin-top: -64px; }

.card--margin-toolbar {
  margin-top: 64px; }

.clickable:hover {
  cursor: pointer; }

aside a.list__tile--active, .menu__content a.list__tile--active {
  border-left: 3px solid; }

aside a.list__tile--active {
  background-color: rgba(0, 0, 0, 0.1); }

.animate.rotation {
  -webkit-animation: rotation .4s infinite ease;
          animation: rotation .4s infinite ease; }

@-webkit-keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.slide-enter-active, .slide-enter {
  -webkit-transition: all .3s ease;
  transition: all .3s ease; }

.slide-enter, .slide-leave-to {
  opacity: 0; }

.slide-enter {
  -webkit-transform: translateX(-3rem);
          transform: translateX(-3rem); }

.slide-leave, .slide-leave-active {
  -webkit-transition: all .4s ease;
  transition: all .4s ease; }

.slide-leave-to {
  -webkit-transform: translateX(3rem);
          transform: translateX(3rem); }

.slide-enter #carbonads, .slide-enter-active #carbonads, .slide-leave #carbonads, .slide-leave-active #carbonads {
  display: none !important; }

.slide-down-enter, .slide-down-leave-to {
  opacity: 0;
  -webkit-transform: translateY(-3rem);
          transform: translateY(-3rem); }

.slide-down-enter-active, .slide-down-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease; }
